import React, { memo } from 'react'
import { useLocation } from 'react-router-dom'
import { NewsContentWrapper } from './style'
import ProductSearch from '@/pages/products/c-cpns/product-search'
import SendForm from '@/components/send-form'
import PrevNext from '@/components/prev-next'
import ShareBtns from '@/components/share-btns'

const Content = memo(() => {
  // 接收路由传参
  const { params } = useLocation().state
  const { date, title, view, content, prev, next} = params
  return (
    <NewsContentWrapper>
      <div className="content_wrap">
        <div className="date_wrap">
          <div className="date_txt">{date}</div>
          <span>|</span>
          <div className="views">View: {view}</div>
        </div>
        <h1>{title}</h1>
        <article>
          {
            content.map((item, index) => (<p key={index}>{item}</p>))
          }
        </article>
        <ShareBtns/>
        <PrevNext prev={prev} next={next} />
        <SendForm/>
      </div>
      <div className="search_wrap">
        <ProductSearch/>
      </div>
    </NewsContentWrapper>
  )
})

export default Content